/* eslint-disable max-len */

import React from 'react';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
  FONT_BODY_BOLD,
} from 'styles/theme';

/**
 * Constants
 */

const EXPERIENCES = [
  {
    company: 'Amazon',
    location: 'Sunnyvale, California',
    role: 'Software Engineer',
    time: 'August 2017 to Present',
  },
  {
    company: 'UCSD CSE Department',
    location: 'La Jolla, California',
    role: 'Undergraduate Tutor',
    time: 'September 2015 to June 2017',
    summary: [
      'Served as a tutor for CSE 105 (Theory of Computability) and CSE 20 (Discrete Mathematics).',
      'Assisted students with homework and understanding class concepts in person during office hours and online on Piazza.',
      'Graded homework and proctored/graded exams in addition to standard tutor duties.',
    ],
  },
  {
    company: 'UCSD Design Lab',
    location: 'La Jolla, California',
    role: 'Undergraduate Research Assistant',
    time: 'January 2017 to May 2017',
    summary: [
      'Worked in a diverse team of researchers, designers, and developers to rapidly produce live prototypes.',
      'Built multiple websites to promote and explain current lab projects to stakeholders.',
    ],
  },
  {
    company: 'West Basin Municipal Water District',
    location: 'Carson, California',
    role: 'Information Technology Intern',
    time: 'July 2014 to September 2014, June 2015 to September 2015',
    summary: [
      'Provided on-site technological support in resolving PC, printer and network problems for an office of 50 people.',
      'Designed and developed a user-friendly contacts database solution for over 5,000 contacts using Microsoft Exchange, resulting in the adaptation of the database across multiple teams.',
      'Developed, tested and deployed a series of command-line scripts in order to expedite daily IT tasks, resulting in the adaptation of these tools across the IT team.',
    ],
  },
];


/**
 * Styled Components
 */

const Header = styled('h2', {
  margin: 0,
  borderBottom: '2px solid black',
  ...FONT_HEADER_BOLD,
});

const ExperienceContainer = styled('div', {
  ':not(:last-child)': {
    marginBottom: '0.5em',
  },
});

const Base = styled('div', {
  width: '50%',
  display: 'inline-block',
});

const Company = styled(Base, {
  ...FONT_BODY_BOLD,
});

const Location = styled(Base, {
  ...FONT_BODY_BOLD,
  textAlign: 'right',
});

const Role = styled(Base, {
  ...FONT_BODY_BOLD,
});

const Time = styled(Base, {
  textAlign: 'right',
  fontStyle: 'italic',
});

const List = styled('ul', {
  margin: 0,
  paddingLeft: '20px',
});

const Placeholder = styled('li', {
  fontStyle: 'italic',
});


/**
 * Experience
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Experience = () => (
  <div>
    <Header>Experience</Header>
    {EXPERIENCES.map(({ company, location, role, time, summary }) => (
      <ExperienceContainer key={company}>
        <Company>{company}</Company>
        <Location>{location}</Location>
        <Role>{role}</Role>
        <Time>{time}</Time>
        <List>
          {summary ? summary.map(item =>
            <li key={item}>{item}</li>
          ) : (
            <Placeholder>To be updated as work progresses</Placeholder>
          )}
        </List>
      </ExperienceContainer>
    ))}
  </div>
);

Experience.propTypes = propTypes;
Experience.contextTypes = contextTypes;
Experience.defaultProps = defaultProps;

export default Experience;



