import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
} from 'styles/theme';

/**
 * Constants
 */

const SKILLGROUP1 = {
  name: 'Programming Languages',
  skills: [
    'JavaScript',
    'C/C++',
    'Java',
    'Python',
    'Haskell',
    'Scala',
  ],
};

const SKILLGROUP2 = {
  name: 'Web Tech/Databases',
  skills: [
    'HTML 5',
    'CSS 3',
    'Node.js',
    'Express',
    'MongoDB',
    'PostgreSQL',
  ],
};

const SKILLGROUP3 = {
  name: 'Web Frameworks/Tools',
  skills: [
    'React',
    'Vue.js',
    'jQuery',
    'D3.js',
    'Sass',
    'Webpack',
  ],
};

const SKILLGROUP4 = {
  name: 'Other Tools',
  skills: [
    'Bash / Fish',
    'Git + GitHub',
    'GDB',
    'Amazon Web Services',
    'Heroku',
    'Google Analytics',
  ],
};


/**
 * Styled Components
 */

const SkillsHeader = styled('h2', {
  margin: 0,
  borderBottom: '2px solid black',
  ...FONT_HEADER_BOLD,
});

const Column = styled('div', {
  width: '25%',
  display: 'inline-block',
});

const SkillGroupHeader = styled('h4', {
  margin: 0,
  fontSize: '1em',
});

const List = styled('ul', {
  margin: 0,
  paddingLeft: '20px',
});


/**
 * SkillGroup
 */

const SkillGroup = ({ name, skills }) => (
  <div>
    <SkillGroupHeader>{name}</SkillGroupHeader>
    <List>
      {skills.map(skill =>
        <li key={skill}>{skill}</li>
      )}
    </List>
  </div>
);

SkillGroup.propTypes = {
  name: PropTypes.string.isRequired,
  skills: PropTypes.arrayOf(
    PropTypes.string.isRequired
  ).isRequired,
};


/**
 * Skills
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Skills = () => (
  <div>
    <SkillsHeader>Skills</SkillsHeader>
    <div>
      <Column>
        <SkillGroup {...SKILLGROUP1} />
      </Column>
      <Column>
        <SkillGroup {...SKILLGROUP2} />
      </Column>
      <Column>
        <SkillGroup {...SKILLGROUP3} />
      </Column>
      <Column>
        <SkillGroup {...SKILLGROUP4} />
      </Column>
    </div>
  </div>
);

Skills.propTypes = propTypes;
Skills.contextTypes = contextTypes;
Skills.defaultProps = defaultProps;

export default Skills;



