import React from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';

import Header from './components/Header/Header';
import Education from './components/Education/Education';
import Coursework from './components/Coursework/Coursework';
import Skills from './components/Skills/Skills';
import Experience from './components/Experience/Experience';
import Projects from './components/Projects/Projects';

/**
 * Helmet Constants
 */

const TITLE = 'David Luu - Resume';


/**
 * Styled Components
 */

const ResumeContainer = styled('div', {
  width: '850px',
});


/**
 * Resume
 */

const propTypes = {};

const contextTypes = {
  router: PropTypes.object.isRequired,
};

const defaultProps = {};

const Resume = () => (
  <ResumeContainer>
    <Helmet title={TITLE}>
      <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Lato:100,400,700|Raleway:500,600" />
    </Helmet>
    <Header />
    <Education />
    <Coursework />
    <Skills />
    <Experience />
    <Projects />
  </ResumeContainer>
);

Resume.propTypes = propTypes;
Resume.contextTypes = contextTypes;
Resume.defaultProps = defaultProps;

export default Resume;



