/* eslint-disable max-len */

import React from 'react';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
  FONT_BODY_BOLD,
} from 'styles/theme';

/**
 * Styled Components
 */

const Header = styled('h2', {
  margin: 0,
  borderBottom: '2px solid black',
  ...FONT_HEADER_BOLD,
});

const Base = styled('div', {
  width: '50%',
  display: 'inline-block',
});

const Degree = styled(Base, {
  ...FONT_BODY_BOLD,
});

const University = styled(Base, {
  ...FONT_BODY_BOLD,
  textAlign: 'right',
});

const Honors = styled(Base);

const Time = styled(Base, {
  textAlign: 'right',
  fontStyle: 'italic',
});

const Link = styled('a', {
  '@media print': {
    textDecoration: 'none',
    color: '#000',
  },
});


/**
 * Education
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Education = () => (
  <div>
    <Header>Education</Header>
    <div>
      <Degree>B.S. Computer Engineering</Degree>
      <University>University of California, San Diego</University>
      <Honors>GPA: 3.88, <Link href="http://www.ece.ucsd.edu/undergraduate/booker-award" target="_blank" rel="noopener noreferrer">Booker Award</Link>, College/Provost Honors</Honors>
      <Time>September 2013 to June 2017</Time>
    </div>
  </div>
);

Education.propTypes = propTypes;
Education.contextTypes = contextTypes;
Education.defaultProps = defaultProps;

export default Education;



