import React from 'react';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
  FONT_BODY_BOLD,
} from 'styles/theme';

/**
 * Styled Components
 */

const HeaderContainer = styled('div', {
  textAlign: 'center',
});

const Name = styled('h1', {
  margin: 0,
  ...FONT_HEADER_BOLD,
});

const Info = styled('span', {
  ...FONT_BODY_BOLD,
});

const Link = styled('a', {
  '@media print': {
    textDecoration: 'none',
    color: '#000',
  },
});

const NoLink = styled('span', {});


/**
 * Header
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Header = () => (
  <HeaderContainer>
    <Name>David Luu</Name>
    <div>
      <Info>Email:</Info> <Link href="mailto:luu.david.c@gmail.com">luu.david.c@gmail.com</Link>
      &nbsp;&middot;&nbsp;
      <Info>Phone:</Info> <NoLink>(213) 255-8890</NoLink>
      &nbsp;&middot;&nbsp;
      <Info>Website:</Info> <Link href="https://davidluu.me" target="_blank" rel="noopener noreferrer">https://davidluu.me</Link>
    </div>
  </HeaderContainer>
);

Header.propTypes = propTypes;
Header.contextTypes = contextTypes;
Header.defaultProps = defaultProps;

export default Header;



