/* eslint-disable max-len */

import React from 'react';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
  FONT_BODY_BOLD,
} from 'styles/theme';

/**
 * Constants
 */

const EXPERIENCES = [
  {
    name: 'Dopelst - Playlist CRUD Web App',
    link: 'https://dopelst-a697d.firebaseapp.com',
    technologies: 'HTML + CSS + JS (Vue.js) + Firebase',
    time: 'Continuous',
    summary: [
      'Developed a modern single-page application using Vue.js, vue-router, and vuefire (for Firebase bindings).',
    ],
  },
  {
    name: 'Pollr - Political Awareness Web App',
    link: 'https://pollrsd.herokuapp.com',
    technologies: 'HTML + CSS + JS + Node.js (Express) + PostgreSQL + MongoDB',
    time: 'March 2016 to June 2016',
    summary: [
      'Developed and deployed the server using Node.js, Express, MongoDB, PostgreSQL, and Heroku.',
      'Developed server-side data manipulation methods and client-side data integration methods in order to efficiently produce D3.js visualizations.',
    ],
  },
  {
    name: 'Personal Portfolio Website',
    link: 'https://davidluu.me/',
    technologies: 'JS (React)',
    time: 'Continuous',
    summary: [
      'Designed my personal website focusing on responsive animation and minimalistic design.',
      'Developed my personal website using React, react-router, and styletron (for CSS).',
    ],
  },
];


/**
 * Styled Components
 */

const Header = styled('h2', {
  margin: 0,
  borderBottom: '2px solid black',
  ...FONT_HEADER_BOLD,
});

const ProjectsContainer = styled('div', {
  ':not(:last-child)': {
    marginBottom: '0.5em',
  },
});

const baseStyle = {
  width: '50%',
  display: 'inline-block',
};

const Base = styled('div', baseStyle);

const Name = styled(Base, {
  ...FONT_BODY_BOLD,
});

const Link = styled('a', {
  ...baseStyle,
  ...FONT_BODY_BOLD,
  textAlign: 'right',
  '@media print': {
    textDecoration: 'none',
    color: '#000',
  },
});

const Technologies = styled(Base, {
  width: '70%',
  ...FONT_BODY_BOLD,
});

const Time = styled(Base, {
  width: '30%',
  textAlign: 'right',
  fontStyle: 'italic',
});

const List = styled('ul', {
  margin: 0,
  paddingLeft: '20px',
});

const Placeholder = styled('li', {
  fontStyle: 'italic',
});


/**
 * Projects
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Projects = () => (
  <div>
    <Header>Projects</Header>
    {EXPERIENCES.map(({ name, link, technologies, time, summary }) => (
      <ProjectsContainer key={name}>
        <Name>{name}</Name>
        <Link href={link} target="_blank" rel="noopener noreferrer">{link}</Link>
        <Technologies>{technologies}</Technologies>
        <Time>{time}</Time>
        <List>
          {summary ? summary.map(item =>
            <li key={item}>{item}</li>
          ) : (
            <Placeholder>To be updated as work progresses</Placeholder>
          )}
        </List>
      </ProjectsContainer>
    ))}
  </div>
);

Projects.propTypes = propTypes;
Projects.contextTypes = contextTypes;
Projects.defaultProps = defaultProps;

export default Projects;



