import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';

import {
  FONT_HEADER_BOLD,
  FONT_BODY_BOLD,
} from 'styles/theme';

import COURSES1 from './data/Courses1.json';
import COURSES2 from './data/Courses2.json';
import COURSES3 from './data/Courses3.json';


/**
 * Styled Components
 */

const Header = styled('h2', {
  margin: 0,
  borderBottom: '2px solid black',
  ...FONT_HEADER_BOLD,
});

const Column = styled('div', {
  width: '33%',
  display: 'inline-block',
});

const CourseContainer = styled('div', {
  fontSize: '0.95em',
});

const Name = styled('span', {
  ...FONT_BODY_BOLD,
});


/**
 * Course
 */

const Course = ({ name, languages }) => (
  <CourseContainer>
    {languages ?
      <span><Name>{name}: </Name> {languages}</span> :
      <Name>{name}</Name>
    }
  </CourseContainer>
);

Course.propTypes = {
  name: PropTypes.string.isRequired,
  languages: PropTypes.string,
};

Course.defaultProps = {
  languages: '',
};


/**
 * Coursework
 */

const courseToComponent = course => <Course key={course.name} {...course} />;

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Coursework = () => (
  <div>
    <Header>Coursework</Header>
    <div>
      <Column>
        {COURSES1.map(courseToComponent)}
      </Column>
      <Column>
        {COURSES2.map(courseToComponent)}
      </Column>
      <Column>
        {COURSES3.map(courseToComponent)}
      </Column>
    </div>
  </div>
);

Coursework.propTypes = propTypes;
Coursework.contextTypes = contextTypes;
Coursework.defaultProps = defaultProps;

export default Coursework;



